import { Component, OnInit } from '@angular/core';

import { ContestService,LoginService } from '../../../_services';

@Component({
  selector: 'app-teacher-contest',
  templateUrl: './teacher-contest.component.html',
  styleUrls: ['./teacher-contest.component.css'],
  providers: [ContestService,LoginService]
})
export class TeacherContestComponent implements OnInit {
  teacher;
  declarations;

  getDeclarations(){
    this.contestService.getDeclarations(this.teacher.teacherno)
      .subscribe(declarations => this.declarations = declarations);
  }

  constructor(private contestService: ContestService, private loginService: LoginService) { }

  ngOnInit() {
    this.teacher = this.loginService.getUser();
    this.getDeclarations();
  }
}



