import { Component, OnInit } from '@angular/core';

import { Student } from '../../../_models';
import { LoginService, StudentService } from '../../../_services';

@Component({
  selector: 'app-student-info',
  templateUrl: './student-info.component.html',
  styleUrls: ['./student-info.component.css'],
  providers: [StudentService]
})
export class StudentInfoComponent implements OnInit {
  student: Student;
  studentEditCopy: Student;
  studentInfo = {
    sno: "",
    name: "",
    gender: "",
    department: "()",
    profession: "",
    classno: "",
    grade: "",
    phone: "",
    email: "",
  }

  disableEdit: boolean = true;

  constructor(private loginService: LoginService, private studentService: StudentService) { }

  ngOnInit() {
    this.student = this.loginService.getUser();
  }

  toggleEdit() {
    this.disableEdit = !this.disableEdit;
  }

  edit() {
    this.studentEditCopy = JSON.parse(JSON.stringify(this.student));
  }

  save() {
    console.log("student-info.save:", this.student);
    this.studentService.update(this.student).subscribe(response => {
      if(!response)
        this.student = this.studentEditCopy;
    });
  }

  cancel() {
    this.student = this.studentEditCopy;
  }
}



