import { Contest } from './contest';
import { Teacher } from './teacher';

// 
export class Schedule {
  scheduleId: number;//id
  competition: Contest;//
  teacher: Teacher;//
  content: string;//
  position: string;//
  date: string;//
  hour: number;//

  constructor(
    scheduleId: number,
    competition: Contest,
    teacher: Teacher,
    content: string,
    position: string,
    date: string,
    hour: number) {
    this.scheduleId = scheduleId;
    this.competition = competition;
    this.teacher = teacher;
    this.content = content;
    this.position = position;
    this.date = date;
    this.hour = hour;
  }

  static newInstance(schedule: Schedule): Schedule {
    return new Schedule(
      schedule.scheduleId,
      schedule.competition,
      schedule.teacher,
      schedule.content,
      schedule.position,
      schedule.date,
      schedule.hour
    )
  }

  // ScheduleId(scheduleId: number): Schedule {
  //   this.scheduleId = scheduleId;
  //   return this;
  // }
  // Competition(competition: Contest): Schedule {
  //   this.competition = competition;
  //   return this;
  // }
  // Teacher(teacher: Teacher): Schedule {
  //   this.teacher = teacher;
  //   return this;
  // }
  // Content(content: string): Schedule {
  //   this.content = content;
  //   return this;
  // }
  // Position(position: string): Schedule {
  //   this.position = position;
  //   return this;
  // }
  // Date(date: string): Schedule {
  //   this.date = date;
  //   return this;
  // }
  // Hour(hour: number): Schedule {
  //   this.hour = hour;
  //   return this;
  // }
}



